.modal_button {
    width: 180px;
    color: white;
    transition: 0.2s;
    font-size: 1rem;
    max-width: 100%;
    padding: 1rem 0.75rem;
    text-decoration: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: rgb(91, 48, 8);
    border-radius: 0.5rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: 400;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    border: 2px solid white;
    color: #fff;
    background-image: linear-gradient(45deg, white 50%, rgb(91, 48, 8) 50%);
    background-position: 100%;
    background-size: 400%;
    transition: background 300ms ease-in-out;
    margin-top: 15%;
}

.modal_button:hover {
    background-position: 0;
    color: rgb(91, 48, 8);
    border: 2px solid rgb(91, 48, 8);
}

.button_vct {
    width: 180px;
    color: white;
    transition: 0.2s;
    font-size: 1rem;
    max-width: 100%;
    padding: 1rem 0.75rem;
    text-decoration: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
    border-radius: 0.5rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: 400;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    border: 2px solid rgb(91, 48, 8);
    color: #fff;
    background-image: linear-gradient(45deg, rgb(91, 48, 8) 50%, transparent 50%);
    background-position: 100%;
    background-size: 400%;
    transition: background 300ms ease-in-out;
    margin-top: 8%;
}

.button_vct:hover {
    background-position: 0;
}


